<template>
  <div class="add-form-container">
    <el-form
      :model="form"
      status-icon
      :rules="rules"
      label-position="left"
      ref="form"
      label-width="100px"
      class="demo-ruleForm"
    >
      <el-form-item v-for="item in formList" :key="item.name" :label="item.label" :prop="item.name">
        <el-input v-model="form[item.name]" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props:{
    form:Object,
  },
  data() {
    return {
       formList:[
        {
          name:'captain',
          label:''
        },
        {
          name:'teamNumber1',
          label:'1'
        },
        {
          name:'teamNumber2',
          label:'2'
        },
        {
          name:'teamNumber3',
          label:'3'
        },
        {
          name:'teamNumber4',
          label:'4'
        },
        {
          name:'qq',
          label:'qq'
        }
      ],
      rules: {
        captain: { required: true, message: "", trigger: "blur" },
        teamNumber1: { required: true, message: "", trigger: "blur" },
        teamNumber2: { required: true, message: "", trigger: "blur" },
        teamNumber3: { required: true, message: "", trigger: "blur" },
        teamNumber4: { required: true, message: "", trigger: "blur" },
        qq: [
          { required: true, message: "qq", trigger: "blur" },
        ],
      },
    };
  },
  methods:{
    submitForm(){
      this.$refs.form.validate((valid,error) =>{
        console.log(valid,error)
        if(valid){
          this.form.id = this.form.captain + this.form.qq;
          console.log(this.form)
          this.$emit('formSubmit',this.form)
        }else{
          this.$message({
            message: '',
            type: 'error',
            showClose: true,
            duration: 3000,
          });
          
        }
      })
      
    },
  }
};
</script>
<style >



</style>
<style lang="less" scoped>
.add-form-container {
  margin: 0 auto;
  width: 80%;
  .el-button{
  width: 100%;
  }
  .el-select.el-select--normal{
    width: 100%;
  }
}
</style>