<template>
  <div class="form-container">
    <el-form
      :rules="rules"
      :model="formData"
      status-icon
      :inline="false"
      ref="form"
      class="form"
      label-width="100px"
      size="normal"
      
    >
      <div class="wrap">
        <div>
          <el-form-item label="" prop="title">
            <el-input v-model="formData.title" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="desc">
            <el-input v-model="formData.desc" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="total">
            <el-input v-model.number="formData.total" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="regType">
            <el-select v-model="formData.regType" placeholder="">
              <el-option v-for="item in regTypeOptions" :key="item.value" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="teamNum">
            <el-input v-model.number="formData.teamNum" placeholder=""></el-input>
          </el-form-item>
        </div>
        <div>
          <el-form-item label="" prop="date">
            <el-date-picker
              v-model="formData.date"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              format="yyyy-MM-dd"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="faculty">
            <el-select v-model="formData.faculty" multiple placeholder="">
              <el-option v-for="item in facultyOptions" :key="item" :label="item" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="qunNum">
            <el-input v-model="formData.qunNum" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="qrcode">
            <el-upload
              action="#"
              list-type="picture-card"
              :on-change="qrcodeUrlChange"
              :auto-upload="false"
              :file-list="fileList"
            >
              
              <i slot="default" class="el-icon-plus"></i>
              <div slot="file" slot-scope="{ file }">
                <img class="el-upload-list__item-thumbnail" :src="file.url" alt />
                <span class="el-upload-list__item-actions">
                  <span
                    class="el-upload-list__item-preview"
                    @click="handlePictureCardPreview(file)"
                  >
                    <i class="el-icon-zoom-in"></i>
                  </span>
                  <span
                    v-if="!disabled"
                    class="el-upload-list__item-delete"
                    @click="handleRemove(file)"
                  >
                    <i class="el-icon-delete"></i>
                  </span>
                </span>
              </div>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" height="100%" :src="dialogImageUrl" alt />
            </el-dialog>
          </el-form-item>
        </div>
      </div>
      <el-form-item>
        <el-button type="primary" @click="onSubmit"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import cloudbase from "@cloudbase/js-sdk";
const app = cloudbase.init({
  env: "xzdj-8gx7xdmm79cf5415"
});

export default {
  props: {
    formData: Object,
    type:String
  },
  data() {
    const validateTeamNum = (rule, value, callback)=>{
      if(this.formData.regType == 'team'){
        if(!value){
          callback(new Error(''));
        }else{
          callback();
        }
      }
    }
    return {
      formItems: [
        { label: "", prop: "title" },
        { label: "", prop: "desc" },
        { label: "", prop: "total" },
        { label: "", prop: "startTime" },
        { label: "", prop: "endTime" },
        { label: "", prop: "faculty" },
        { label: "", prop: "qunNum" },
        { label: "", prop: "qrcode" }
      ],
      rules: {
        title: { required: true, message: "", trigger: "blur" },
        desc: { required: true, message: "", trigger: "blur" },
        total: [
          { required: true, message: "", trigger: "blur" },
          { type: 'number',  message: "", trigger: "change" },
        ],
        regType: { required: true, message: "", trigger: "blur" },
        teamNum: [
          { validator: validateTeamNum, trigger: 'blur' },
          { type: 'number',  message: "", trigger: "change" },
        ],
        date: { required: true, message: "", trigger: "blur" },
        faculty: { required: true, message: "", trigger: "blur" },
        qunNum: [
          { required: true, message: "", trigger: "blur" },
        ],
        qrcode: {
          required: true,
          message: "",
          trigger: "blur"
        }
      },
      facultyOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      regTypeOptions: [{value:"team",label:''}, {value:"personal",label:''}],
      fileList: [],
      dialogImageUrl: "",
      dialogVisible: false,
      disabled: false,
    };
  },
  watch: {
    dialogImageUrl() {
      console.log(this.dialogImageUrl);
    },
  },
  mounted() {
    if(!this.type){
      return;
    }
    // 
    app.getTempFileURL({
      fileList: [this.formData.qrcode]
    }).then(res=>{
      this.fileList = [{name:res.fileList[0].fileID,url:res.fileList[0].tempFileURL}];
    })
  },
  methods: {
    // 
    qrcodeUrlChange(file, fileList) {
      console.log(file, fileList);
      this.fileList.push(file);
      this.formData.qrcodeUrl = file.url;
      app
        .uploadFile({
          cloudPath: `/activity/${this.formData.title}-${new Date().getTime()}-${file.name}`,
          filePath: file.raw
        })
        .then(result => {
          // 
          this.formData.qrcode = result.fileID;
          
          console.log(this.formData)
          // console.log(resfileID)
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 3000
          });
          console.log(result);
        }).catch(()=>{
          this.$message({
            message: "",
            type: "success",
            showClose: true,
            duration: 3000
          });
        })
    },
    // 
    handleRemove(file) {
      console.log(file);
      this.fileList = this.fileList.filter(item => file !== item);
      this.formData.qrcode = '';
    },
    // 
    handlePictureCardPreview(file) {
      this.dialogVisible = true;
      this.dialogImageUrl = file.url;
    },
    // 
    onSubmit() {
      console.log(this.formData);
      // 
      this.formData.startTime = this.formData.date[0];
      this.formData.endTime = this.formData.date[1];
      console.log(this.formData.startTime, this.formData.endTime);
      
      if(!this.type){
        this.formData.regList = [];//
        this.formData.teamList = [];
      }
      
      this.$refs.form.validate((valid, error) => {
        if (valid) {
          console.log(valid);
          if(this.type){
            this.$emit('editActivitySubmit',this.formData)
          }else{
            this.$emit("formSubmit", this.formData);
          }
        } else {
          const err = Object.values(error)[0][0].message;
          this.$message({
            message: err,
            type: "error",
            showClose: true,
            duration: 3000
          });
        }
      });
    }
  }
};
</script>
<style lang="less">
.form-container .wrap {
  .el-upload-list--picture-card .el-upload-list__item {
    width: 90px;
    height: 90px;
    > div {
      height: 100%;
    }
  }
  .el-upload--picture-card {
    width: 90px;
    height: 90px;
    line-height: 100px;
  }
}
</style>
<style lang="less" scoped>
.form-container {
  margin: 0 auto;
  width: 80%;
  text-align: center;
  .wrap {
    display: flex;
    justify-content: center;
    > div:nth-of-type(1) {
      margin-right: 20px;
    }
    .el-date-editor.el-range-editor {
      width: 100%;
      margin: 0 auto;
    }

    .el-form-item {
      width: 400px;
    }
  }

  .el-button {
    width: 200px;
  }
  .el-select.el-select--normal {
    width: 100%;
  }
}
</style>