<template>
  <div class="login-container">
    <el-form
      class="form"
      :model="form"
      ref="form"
      status-icon
      :rules="rules"
      label-width="80px"
      :inline="false"
      size="normal"
      label-position="left"
      
    >
      <el-form-item label="" prop="name">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" v-model="form.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small" @click="onSubmit"  @keydown.enter="onSubmit"
          ></el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {setStorage} from '../api/user'
export default {
  data() {
    return {
      form: {
        name: "",
        password: "",
      },
      rules: {
        name: { required: true, message: "", trigger: "blur" },
        password: { required: true, message: "", trigger: "blur" },
      },
    };
  },
  methods: {
    async onSubmit() {
      console.log("");
      this.$refs.form.validate((valid) => {
        if (valid) {
          if (
            (this.form.name === "liushuaiyang" ||
              this.form.name === "admin_one" ||
              this.form.name === "admin_two") &&
            this.form.password === "xzdjyyds"
          ) {
            this.$message({
              message: "",
              type: "success",
              duration: 3000,
            });
            setStorage(this.form.name);
            this.$store.commit('setUser', this.form.name)
            this.$router.push('/club/list');
          }
          else{
            this.$message({
              message: "",
              type: "error",
              duration: 3000
            })
          }
        } else {
          this.$message({
            message: "",
            type: "error",
            duration: 3000,
          });
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.login-container {
  display: flex;
  justify-content: center;
  margin-left: -80px;
  .form {
    margin-top: 50px;
    text-align: center;
  }
}
</style>