<template>
  <div class="club-list-container">
    <div class="search">
      <div class="left">
        <el-input
          class="searchInput"
          v-model="searchValue"
          placeholder=""
          size="normal"
          clearable
        ></el-input>
        <el-button
          class="searchBtn"
          type="primary"
          size="small"
          icon="el-icon-search"
          @click="searchSubmit"
          ></el-button
        >
        <el-button v-if="searchBackShow" type="info" size="small" @click="searchBack"></el-button>
        
      </div>
      <div class="right">
        <el-button type="primary" size="small" @click="toClubAddPage"
          ></el-button
        >
      </div>
    </div>
    <div class="club-table" ref="table" v-loading="tableData.length == 0 && isLoading">
      <el-table :data="tableData" highlight-current-row stripe border>
        <el-table-column
          v-for="col in columns"
          :prop="col.id"
          :key="col.id"
          :label="col.label"
          align="center"
        >
        </el-table-column>
        <el-table-column label="" align="center">
          <template slot-scope="scope">
            <el-button size="mini" @click="handleEdit(scope.$index, scope.row)"
              ></el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(scope.$index, scope.row)"
              ></el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="pager">
      <el-pagination
        background
        layout=" total , prev, pager, next"
        :page-size="20"
        :total="totalData.length"
        :current-page.sync="currentPage"
        :current-change="currentChange"
      >
      </el-pagination>
    </div>
    <el-dialog title="" center :visible.sync="dialogVisible" width="30%">
      <EditForm :formData="editFormData" @formSubmit="editFormSubmit" />
    </el-dialog>
  </div>
</template>

<script>
import cloudbase from "@cloudbase/js-sdk";
import EditForm from "@/components/club/EditForm";
const app = cloudbase.init({
  env: "xzdj-8gx7xdmm79cf5415",
});
const db = app.database();
export default {
  components: {
    EditForm,
  },
  data() {
    return {
      isLoading:true,
      searchValue: "",
      searchBackShow:false,
      columns: [
        {
          label: "",
          id: "name",
        },
        {
          label: "",
          id: "grade",
        },
        {
          label: "",
          id: "faculty",
        },
        {
          label: "",
          id: "class",
        },
        {
          label: "",
          id: "mobile",
        },
      ],
      totalData: [],
      currentPage: 1,
      dialogVisible: false,
      editFormData: [],
    };
  },
  computed: {
    tableData() {
      return this.totalData.filter((item, i) => {
        return i >= (this.currentPage - 1) * 20 && i < this.currentPage * 20;
      });
    },
  },
  watch: {
    // currentPage 
    currentPage() {
      // console.log(this.$refs.table)
      this.$refs.table.scrollTop = 0;
    },
    searchValue(){
      if(this.searchValue == ''){
        this.isLoading = true;
        this.getDatabase();
      }
    }
  },
  methods: {
    searchBack(){
      this.searchBackShow = false;
      this.searchValue = '';
      this.isLoading = true;
      this.getDatabase();
    },
    // 
    searchSubmit() {
      this.searchBackShow = true;
      this.totalData = this.totalData.filter((item) => {
        // Object.values(item).indexOf(this.searchValue) > 0
        const res = Object.values(item).filter((i) => {
          return i.indexOf(this.searchValue) >= 0;
        });
        return res.length > 0
      });
      if(this.totalData.length == 0){
        this.isLoading = false;
      }
      
      // console.log(this.totalData)
    },
    // 
    async handleDelete(index, row) {
      console.log(index, row);
      // const id = row.id;
      this.$confirm(`${row.name}`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          this.totalData = this.totalData.filter((item) => item.id !== row.id);
          console.log(this.totalData);
          // 
          this.updateDatabase();
          this.$message({
            type: "success",
            message: "!",
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    editFormSubmit(form) {
      this.dialogVisible = false;
      console.log(form);
      this.totalData = this.totalData.map((item) => {
        if (item.id === form.id) {
          item = form;
        }
        return item;
      });
      this.updateDatabase();
    },
    // 
    handleEdit(index, row) {
      console.log(index, row);
      this.dialogVisible = true;
      this.editFormData = { ...row }; //{...}rowrow
    },
    // 
    toClubAddPage() {
      this.$router.push({ name: "ClubAdd" });
    },
    // 
    currentChange(page) {
      console.log(this.currentPage);

      this.currentPage = page;
    },
    // 
    updateDatabase() {
      db.collection("community")
        .doc("clubList")
        .update({
          data: this.totalData,
        })
        .then((res) => {
          console.log(res);
        });
    },
    // 
    getDatabase() {
      db.collection("community")
        .doc("clubList")
        .get()
        .then((res) => {
          const result = res.data[0].data;
          console.log(result)
          const _totalData = result.map(item =>{
            item.mobile =parseInt(item.mobile)
            item.grade = item.fac[0];
            item.faculty = item.fac[1];
            return item;
          })
          this.totalData = _totalData;
          console.log(_totalData)
        });
    },
  },

  async mounted() {
    // 
    this.getDatabase();
  },
};
</script>

<style lang="less" scoped>
.club-list-container {
  .search {
    padding: 20px;
    border-bottom: 1px solid #eee;
    display: flex;
    align-items: center;
    justify-content: space-between;
    .searchInput {
      width: 250px !important;
      margin-right: 20px;
    }
  }
  .club-table {
    // height: 70vh;
    overflow: auto;
  }
  .pager {
    text-align: right;
    margin: 10px 20px;
  }
}
</style>